<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" ModuleName="CRM" SubModuleCode="CRM_APPROVAL" :RenderRowActionMethod="renderActions" 
            v-on:ActionButtonClick="actionButtonClick" IdentityColumn="id" :HeaderText="$t('CampaignApproval')" :listType="['List']" 
            :ListData="ApprovalData" :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecord" 
            :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchData" :showCheckBox="true" :SortExp="SortExp" :LegendArray="LegendArray"
            :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch" :CurrentPageNumber="PageNumber"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{data}">
                <template v-if="data.column.COLUMN_NAME == 'status_id'"> 
                    <listing-status :isfor="'CRM_APPROVAL'" v-if="isCRMShowStausDrop(data.row.status_id, 'CRM') == true"
                        ref="updatestatus" v-bind:dataid="data.row.id" data-controller="CRM"
                        v-bind:statuslist="StatusListForListing" v-bind:item="data" data-action="CampaignApproval"
                        v-bind:dataselected="data.row.status_name" v-bind:callbackfunction="FetchData" 
                        v-bind:isselectedvaluestring="true">
                    </listing-status>
                    <a class="inactive-disabled" v-if="isCRMShowStausDrop(data.row.status_id, 'CRM') == false">
                        <span v-bind:class="getStatusClass(data.row.status_id, 'status_id')">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                    </a>
                 </template>
                 <template v-if="data.column.COLUMN_NAME == 'created_at'">
                    {{ $options.filters.formatDate(data.row[data.column.COLUMN_NAME]) }}
                </template>
            </template>
        </tg-list>
    </section>
</template> 

<script>
import DataService from '../../../services/DataService';
import Schema from '../CampaignApproval/CampaignApprovalSchema';

export default {
    components: {
        
    },
    data() {
        return {
            privilegeParams: null,
            StatusListForListing:[],
            LegendArray: [],
            isLoading: false,
            ApprovalData: [],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            TotalRecord: 0,
            SortExp: '',
            SortBy: '',
            PageNumber: 1,
            PageSize: 10,
            TotalPages: 0,
            CurrentPage: 1,
            postContent: '',
            campaignOwnerId: '',
            approverId: '',
            status: '',
            dateto: '',
            datefrom: '',
            Headers: [{
                    COLUMN_NAME: "created_at",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Date",
                    DISPLAY_ORDER: 1
                },
                {
                    COLUMN_NAME: "content",  
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Post Description",
                    DISPLAY_ORDER: 2
                },
                {
                    COLUMN_NAME: "created_by",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Created By",
                    DISPLAY_ORDER: 3
                },
                {
                    COLUMN_NAME: "status_id",
                    DATA_TYPE: "text",
                    SORTABLE: true,
                    VISIBLE: true,
                    DISPLAY_NAME: "Status",
                    DISPLAY_ORDER: 4
                }
            ]
        };
    },
   async created() {
        var vm=this;
        vm.getStatuslistForListing("CRM_APPROVER", "en");
        vm.privilegeParams = `controller=CRM&action=`;
        vm.LegendArray = [
                {
                    class: 'bg-yellow colorboxsquare',
                    text: (vm.$t('PendingRequest'))
                },
                {
                    class: 'bg-red colorboxsquare',
                    text: (vm.$t('RejectedRequest'))
                }
            ],
        vm.Headers.forEach((column) => {
                column.settings = null;
                if (column.COLUMN_NAME == 'status_id' || column.COLUMN_NAME =='created_at') {
                    column.settings = {
                        isInSlot: true
                    }
                }
        });
       await vm.FetchData();
       await Schema.bindleftCommonSearchdropdown(vm, "CRM", "CAMPAIGN_OWNER_SEARCH");
    },
    methods: {
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (s) {
          var SortDir = "sort tb_headerSortDown";
           this.SortBy = s;
                if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else
                {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                }
                this.FetchData();
                $("#" + s+'  span:first-child').attr("class", SortDir);
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "approve":
                    this.approve();
                    break;
                case "reject":
                    this.reject();
                    break;
            }
        },
        actionButtonClick: function (callbackfunction, item) {
           var vm = this;
            switch (callbackfunction) {
                case 'OpenModalAddSMTP':
                    vm.OpenModalAddSMTP();
                    break;
                case 'Delete':
                    vm.commonDeleteData();
                    break;
            }
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url = "langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
            DataService.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        },
        FetchData: async function () {
           var vm = this;
           vm.isLoading = true; 
           //var url =`postContent=${vm.postContent}&campaignOwnerId=${vm.campaignOwnerId}&approverId=${vm.approverId}&datefrom=null&dateto=${vm.dateto}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`;
           var url = '';
            await DataService.CampaignApprovalListing(url).then(response=>{
            vm.ApprovalData=[];
            if(response.data != ''){
                vm.ApprovalData = response.data;
                vm.ApprovalData.forEach((row)=>{
                    row.isCheckBoxDisabled=false;
                    if(row.status_code=='CRM_APPROVE' || row.status_code=='CRM_REJECT'){
                        row.isCheckBoxDisabled=true;
                    } 
                    row.additionalClass = '';
                    row.status_code == 'CRM_REJECT' ? row.additionalClass = 'danger-bar' : row.additionalClass = 'warning-bar';
                });
                vm.TotalRecord = response.data[0].total;
                vm.TotalPages = Math.ceil(vm.TotalRecord / vm.PageSize);
                vm.CurrentPage = vm.PageNumber;
                vm.noRecord = false;
            }
            else {
                vm.noRecord = true;
            }
           })
           setTimeout(function () {
                vm.ResponsiveDataTable('tablelistingdata')
            }, 500);
            vm.isLoading = false;  
        },
        reject: function(){
            var vm=this;
            DataService.CheckPrivilege(vm.privilegeParams + 'RejectInward')
                .then(function (response) {
                    if (response.data) {
                        if ($(event.target).hasClass("disabled") || $(event.target).closest("a.iconenable").hasClass("disabled")) {
                            return false;
                        }
                        var val = "";
                        if (typeof (id) !== "undefined" && id != null) {
                            val = id;
                        }
                        else {
                            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                                if (val.length > 0)
                                    val += ',';
                                val += $(this).val();
                            });
                        }
                        if (val.length > 0 || val != "") {
                            vm.confirmR(vm.$t("AreYouSureToRejectSelectedRecords"), true, (vm.$t("Reject") + "  " + vm.$t("CampaignApproval")), function () {
                                var postJSON = {
                                    ids: val,
                                    moduleName: "CRM",
                                    subModuleCode: "CRM_APPROVAL_PENDING",
                                    approvaltype: 'CRM_REJECT'
                                };
                                var objectPostString = JSON.stringify(postJSON);
                                DataService.ApproveRejectCampaign(objectPostString).then(function (response) {
                                    
                                        var box = "";
                                        if (response.data == 0) {
                                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                            box = vm.ShowAlert(vm.$t('RejectedSuccessfully', [vm.$t('CampaignApproval')]), "success", true, vm.$t("Alert"));
                                            vm.FetchData();
                                            $('.statuschange').addClass('disabled');
                                        }
                                        else {
                                            box = vm.ShowAlert(vm.$t('RejectError', [vm.$t('CampaignApproval')]), "failure", true, vm.$t("Alert"));
                                        }
                                        // setTimeout(function () {
                                        //     box.modal('hide');
                                        // }, 2000);

                                    }, function (response) {

                                    });
                            });
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t("RemoveReject"), "warning", true, vm.$t("Alert"));
                        }
                    }
                    else {
                        vm.ShowAlert('You are not authorized', "failure", true, "Alert");
                    }
                });
        },
        approve: function () {
                var vm = this;
                var val = "";
                if (typeof (this.id) !== "undefined" && this.id != null) {
                    val = this.id;
                }
                else {
                    $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                        if (val.length > 0)
                            val += ',';
                        val += $(this).val();
                    });
                }
                if (val.length > 0 || val != "") {
                    vm.confirmR(vm.$t("AreYouSureToApproveSelectedRecords"), true, vm.$t("Approve",[vm.$t("CampaignApproval")]), function () {
                        var postJSON = {
                            ids: val,
                            moduleName: "CRM",
                            subModuleCode: "CRM_APPROVAL_PENDING",
                            approvaltype: 'CRM_APPROVE'
                        };
                        var objectPostString = JSON.stringify(postJSON);
                        DataService.ApproveRejectCampaign(objectPostString).then(function (response) { 
                            var box = "";
                            if (response.data == 0) {
                                $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                                box =    vm.ShowAlert(vm.$t('ApprovedSuccessfully', [vm.$t('CampaignApproval')]), "success", true, vm.$t("Alert")); 
                                vm.FetchData();
                                $('.statuschange').addClass('disabled');
                            }
                            else {
                                box = vm.ShowAlert(vm.$t("Error Occur while Approving Record"),"warning", true, vm.$t("Alert"));
                            }
                        }, function (response) {
                        });
                    });
                }
                else {
                    vm.ShowAlert(vm.$t("LeaveApprove"),"warning", true, vm.$t("Alert"));
                }
        },
    }
}
</script>