<template>
    <section>
        <div class="modal-body">
            <div class="col-sm-12 col-md-6 p-0 float-right bg-light p-4">
                <div class="mobilebase mx-auto">
                    <div class="col-12 bg-light p-2 border"><em class="fa fa-bars text-secondary"></em></div>
                    <div class="col-12 p-2 bg-light border" style="min-height:350px;">
                        <div class="col-12" id="textcontent">{{FormSchema[2].Data[0].value}}</div>
                    </div>
                </div>
                <div class="col-12 float-left mt-3">
                    <div class="input-group">
                        <span class="w-100 d-flex">
                            <input type="text" placeholder="Enter Mobile Number" v-model="MobileNumber" class="form-control w-70 mr-2" autocomplete="off" />
                            <a class="btn btn-success" placeholder="Mobile Phone Number required" @click="SendTest">Send Test</a>
                        </span>
                    </div>
                </div>
            </div>
            <dynamic-form :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" />
        </div>
    </section>
</template>
    
<script>
import CampaignDataServices from "../../../services/DataService";
    
export default {
    props: { 
        GetTemplateData:  {
            type: Object,
        },
        item:  {
            type: String
        }
   },
    data() {
        return {
            AttachData:[],
            CustomValue: [],
            FileName: null,
            TiggerData: [],
            MobileNumber: '',
            TEMPLATE_ID: 0,
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('name'),
                        name: "template_name",
                        value: "",
                        placeholder: "Enter Campaign Name",
                        config: {},
                        validationRules: { "required": true }
                    }]
                },
                {
                    layoutType: "double",
                    Data: [{
                            astype: "SelectField",
                            label: this.$t('CustomValue'),
                            name: "CustomValue",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                onChange: this.onChangeCustomTrigger
                            },
                            validationRules: { "required": false }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('TriggerLink'),
                            name: "TiggerData",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                onChange: this.onChangeCustomTrigger
                            },
                            validationRules: { "required": false }
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Content'),
                        name: "template_content",
                        value: "",
                        placeholder: "Enter Content",
                        config: {
                            options: [],
                        },
                        validationRules: { "required": false }
                    }]
                },
                {
                    layoutType: "single",
                    Data: [ {
                            astype: "FileField",
                            label: this.$t('FileControlText'),
                            name: "attachments",
                            value: "",
                            placeholder: "Select",
                            config: {
                                maxSize:5,
                                accept:[
                                    {
                                       fileType:'png',
                                       iconClass:'fa-file-image-o'
                                    },
                                    {
                                       fileType:'jpg',
                                       iconClass:'fa-file-image-o'
                                    },
                                    {
                                       fileType:'jpeg',
                                       iconClass:'fa-file-image-o'
                                    },
                                    {
                                       fileType:'pdf',
                                       iconClass:'fa-file-archive-o'
                                    }
                                ],
                                onChange: this.GetBase64String,
                                options: [],
                                multiple: true
                            },
                            validationRules: { "required": false }
                        },
                        
                    ]
                },
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    onclick: this.onSubmit
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closepopup,
                }
            ]
        }
    },
    created() {
        var vm = this;
        vm.GetCustomFieldValue();
        vm.GetTriggerFieldValue();
        if(vm.GetTemplateData != '{}'){
            vm.EditSmsTemplate();
        }
    },
    methods: {
        EditSmsTemplate(){
            var vm = this;
            vm.TEMPLATE_ID = vm.GetTemplateData.TEMPLATE_ID;
            vm.FormSchema[0].Data[0].value = vm.GetTemplateData.TEMPLATE_NAME;
            vm.FormSchema[2].Data[0].value = vm.GetTemplateData.TEMPLATE_CONTENT;
            vm.FormSchema[3].Data[0].value = vm.GetTemplateData.ATTACHMENT_COUNT;
        },
        closepopup() {
            var vm = this;
            vm.$parent.$parent.ClosemodalAddSMSCampaign();
        },
        GetCustomFieldValue() {
            var vm = this;
            var url = 'MODULE_NAME=CRM&SUB_MODULE_CODE=CRM_CAMPAIGNS,CRM_CONTACTS';
            CampaignDataServices.GetCustomFieldValue(url).then(function (response) {
                vm.CustomValue = response.data;
                vm.CustomValue.forEach(item=>{
                    vm.FormSchema[1].Data[0].config.options.push({
                        name: item.DISPLAY_NAME,
                        value: item.NAME
                    })
                })
            });
        },
        GetTriggerFieldValue() {
            var vm = this;
            var url = 'TYPE=CRM_TRIGGER&MODULE_NAME=null';
            CampaignDataServices.GetTriggerFieldValue(url).then(function (response) {
                vm.TiggerData = response.data;
                vm.TiggerData.forEach(item=>{
                    vm.FormSchema[1].Data[1].config.options.push({
                        name: item.NAME,
                        value: item.LINK
                    })
                })
            })
        },
        onChangeCustomTrigger(e) {
            var vm = this;
            $('#template_content .form-control').focus();
            if(typeof(vm.FormSchema[2].Data[0].value) =='undefined'){
                vm.FormSchema[2].Data[0].value='';
            }
            vm.FormSchema[2].Data[0].value += e.target.value+ ' ';
        },
        SendTest() {
            var vm = this;
            var obj = {
                Send_sms: vm.FormSchema[2].Data[0].value,
                Send_to: vm.MobileNumber,
            }
            var url = JSON.stringify(obj);
            CampaignDataServices.SendTwilioSMS(url).then(function(response){
                debugger;
            })
        },
        onSubmit(formfields, val){
            var vm = this;
            vm.isLoading = true; 
            var obj ={
                template_id: vm.TEMPLATE_ID,
                template_name: formfields.template_name,
                attachments: vm.AttachData,
                template_content: formfields.template_content,
            }
            var url = JSON.stringify(obj);
            CampaignDataServices.ManageSMSCampaign(url).then(function(response){
                if (response.data.MSG == 'INSERT') {
                    vm.ShowAlert(vm.$t('AddedSuccess', [vm.$t('Campaign')]), "Success", true, vm.$t("Alert"));
                    vm.$parent.$parent.FetchData()
                    vm.closepopup();
                }
                else if (response.data.MSG == 'UPDATE'){
                    vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Campaign')]), "Success", true, vm.$t("Alert"));
                    vm.$parent.$parent.FetchData()
                    vm.closepopup();
                }
                else if (response.data.MSG == 'ALREADY_EXISTS'){
                    vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Campaign')]), "warning", true, vm.$t("Alert"));
                }
                else {     
                    vm.ShowAlert(vm.$t('AddedError',[vm.$t('Campaign')]),"failure", true, vm.$t("Alert"));
                }
            })
        },
        GetBase64String: function (evt) {
            try {
                var vm = this;
                //alert(vm.apiurl);
                vm.fileData = [];

                var f = evt.target.files[0]; // FileList object
                var reader = new FileReader();
                var attachmentData={
                    attachement_name:evt.target.files[0].name,
                    attachement_path:"",
                    attachement_mime:evt.target.files[0].type,
                    created_by:769083
                }
                vm.AttachData.push(attachmentData);
                // Closure to capture the file information.
                reader.onload = (function (theFile) {

                    return function (e) {
                        var binaryData = e.target.result;
                        //Converting Binary Data to base 64
                        var base64StringEncoded = window.btoa(binaryData);
                        //showing file converted to base64

                        var fileName = f.name;
                        vm.fileName = fileName;
                        var n = fileName.lastIndexOf(".");
                        var extensionName = fileName.substring(n + 1, fileName.length);

                        vm.fileExt += "." + extensionName;
                        var attachemnetMime = '{"ContentType":"' + f.type + '","Size":"' + f.size + '","Extension":"' + extensionName + '"}';
                        var fileSize = f.size;
                        vm.fileSize = fileSize;

                        // var ggg = vm.configData;
                        var fileObj = {
                            attachement_mime: attachemnetMime,
                            fileName: fileName,
                            base64String: base64StringEncoded,
                            fileSize: f.size,
                            contentType: f.type
                        };
                        vm.fileData.push(fileObj);
                    };
                })(f);
                // Read in the image file as a data URL.
                reader.readAsBinaryString(f);
            }
            catch (ex) {
            }
        },
        setFile: function (e) {
            var vm = this;
            vm.file = e.target.files[0];
            vm.FileName = e.target.files[0].name;
        },
    },
}
</script>
    